// Import external
import React from 'react';
import {StyleProp, TextProps, View, ViewStyle} from 'react-native';
import {isUndefined} from 'lodash';
import {
  ApplicationProvider,
  Icon,
  IconRegistry,
  TopNavigation,
  TopNavigationAction,
} from '@ui-kitten/components';
import {RenderProp} from '@ui-kitten/components/devsupport';
import {useNavigation} from '@react-navigation/native';
import * as eva from '@eva-design/eva';
import {EvaIconsPack} from '@ui-kitten/eva-icons';
import {default as theme} from '../../../custom-theme.json'; // <-- Import app theme
import {default as mapping} from '../../../mapping.json'; // <-- Import app mapping
type Props = {
  style?: StyleProp<ViewStyle>;
  title?: RenderProp<TextProps> | React.ReactText | undefined;
  subtitle?: RenderProp<TextProps> | React.ReactText | undefined;
  onPressIconLeft?: () => void;
  viewIconLeft?: string;
  contentStyle?: StyleProp<ViewStyle>;
  renderContentRight?: () => React.ReactElement;
  appearance?: 'default' | 'control';
  alignment?: 'start' | 'center';
  fillIconBackLeft?: string;
};

const HeaderCM = ({
  style,
  onPressIconLeft,
  viewIconLeft = 'arrow-back',
  title = 'quangnh39',
  contentStyle,
  renderContentRight,
  appearance = 'default',
  alignment = 'center',
  subtitle = '',
  fillIconBackLeft,
}: Props) => {
  const navigation = useNavigation();

  const handleOnpressIconLeft = () => {
    if (isUndefined(onPressIconLeft)) {
      navigation.goBack();
    } else {
      onPressIconLeft();
    }
  };

  const renderBackAction = () => (
    <TopNavigationAction
      hitSlop={15}
      onPress={handleOnpressIconLeft}
      icon={<Icon name={viewIconLeft} fill={fillIconBackLeft} />}
    />
  );

  return (
    <>
      {/* <IconRegistry icons={[EvaIconsPack]} /> */}
      <ApplicationProvider
        {...eva}
        theme={{...eva.light, ...theme}}
        customMapping={mapping as any}>
        <View style={[style]}>
          <TopNavigation
            title={title}
            appearance={appearance}
            accessoryLeft={renderBackAction}
            accessoryRight={renderContentRight}
            alignment={alignment}
            subtitle={subtitle}
            style={[
              contentStyle,
              {
                marginTop: 46,
              },
            ]}
          />
        </View>
      </ApplicationProvider>
    </>
  );
};

export default HeaderCM;
